[data-theme=huddle] {
  --button-size: 3.125rem;
  --lk-primary-color: #e23067;
  --lk-primary-bg: whitesmoke;
  --lk-secondary-color: lightgrey;
}
[data-theme=huddle] .button {
  border: 0px;
  box-shadow: 0 0 1px rgba(29, 28, 29, 0.13), 0 1px 3px 0 rgba(0, 0, 0, 0.08);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 60%;
  background-color: var(--lk-primary-bg);
  cursor: pointer;
}
[data-theme=huddle] .button[aria-pressed=false] {
  background-color: var(--lk-primary-bg);
}
[data-theme=huddle] .button[data-source] {
  height: var(--button-size);
  width: var(--button-size);
  aspect-ratio: 1/1;
  border-radius: 99999px;
}
[data-theme=huddle] .disconnect-button {
  width: calc(1.5 * var(--button-size));
  height: var(--button-size);
}
