.lk-audio-conference {
  position: relative;
  width: 100%;
  height: 100%;
}

.lk-audio-conference-stage {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.lk-chat {
  display: grid;
  grid-template-rows: var(--lk-chat-header-height) 1fr var(--lk-control-bar-height);
  width: clamp(200px, 55ch, 60ch);
  background-color: var(--lk-bg2);
  border-right: 1px solid var(--lk-border-color);
  align-items: end;
}

.lk-chat-header {
  height: var(--lk-chat-header-height);
  padding: 0.75rem;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.lk-chat-header .lk-close-button {
  position: absolute;
  left: 0;
  transform: translateX(50%);
  background-color: rgba(0, 0, 0, 0);
}

.lk-chat-header .lk-close-button:hover {
  background-color: var(--lk-control-active-hover-bg);
}

.lk-chat-messages {
  display: flex;
  width: 100%;
  max-height: 100%;
  flex-direction: column;
  gap: 0.25rem;
  overflow: auto;
}

.lk-chat-entry {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  margin: 0 0.25rem;
}

.lk-chat-entry .lk-meta-data {
  font-size: 0.75rem;
  color: var(--lk-fg5);
  white-space: nowrap;
  padding: 0 0.3rem;
  display: flex;
}

.lk-chat-entry .lk-meta-data .lk-participant-name {
  margin-top: 1rem;
}

.lk-chat-entry .lk-meta-data .lk-timestamp {
  margin-right: auto;
  align-self: flex-end;
}

.lk-chat-entry .lk-edit-button {
  background: none;
  float: left;
  margin: 0;
  padding: 0 0.25rem;
  border-radius: 0;
  font-size: 12px;
}

.lk-chat-entry .lk-message-body {
  display: inline-block;
  border-radius: 15px;
  padding: 0.25rem 0.75rem;
  word-break: break-word;
  width: -moz-fit-content;
  width: fit-content;
  max-width: calc(100% - 32px);
}

.lk-chat-entry[data-lk-message-origin=local] .lk-message-body {
  background-color: var(--lk-bg5);
}

.lk-chat-entry[data-lk-message-origin=remote] .lk-message-body {
  background-color: var(--lk-accent4);
}

.lk-chat-entry a {
  text-decoration: underline;
  color: inherit;
}

.lk-chat-entry:last-child {
  margin-bottom: 0.25rem;
}

.lk-chat-form {
  display: flex;
  gap: 0.75rem;
  padding: 0.75rem;
  border-top: 1px solid var(--lk-border-color);
  max-height: var(--lk-control-bar-height);
}

.lk-chat-form-input {
  width: 100%;
}

@media (max-width: 600px) {
  .lk-chat {
    position: fixed;
    top: 0;
    left: 0;
    max-width: 100%;
    bottom: var(--lk-control-bar-height);
  }
}
.lk-control-bar, .lk-agent-control-bar {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  justify-content: center;
  padding: 0.75rem;
  border-top: 1px solid var(--lk-border-color);
  max-height: var(--lk-control-bar-height);
}

.lk-agent-control-bar {
  height: var(--lk-control-bar-height);
  --lk-bg: transparent;
  --lk-va-bar-width: 2px;
  --lk-va-bar-gap: 4px;
  --lk-va-bar-border-radius: 1px;
}

.lk-agent-control-bar .lk-audio-bar-visualizer .lk-audio-bar.lk-highlighted {
  filter: none;
}

.lk-prejoin {
  background-color: var(--lk-bg);
  line-height: var(--lk-line-height);
}

[data-lk-theme] {
  font-size: var(--lk-font-size);
  font-family: var(--lk-font-family);
  color: var(--lk-fg);
}

[data-lk-theme] button, [data-lk-theme] input {
  font: inherit;
  line-height: inherit;
}

[data-lk-theme] .lk-button {
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}

[data-lk-theme] .lk-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

[data-lk-theme] .lk-form-control {
  font-family: var(--lk-font-family);
  padding: 0.625rem 1rem;
  background-color: var(--lk-control-bg);
  border: 1px solid var(--lk-border-color);
  border-radius: var(--lk-border-radius);
}

.lk-prejoin {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  gap: 1rem;
  margin-inline: auto;
  background-color: var(--lk-bg);
  width: min(100%, 480px);
  align-items: stretch;
}

.lk-prejoin .lk-video-container {
  position: relative;
  width: 100%;
  height: auto;
  aspect-ratio: 16/10;
  background-color: #000;
  border-radius: var(--lk-border-radius);
  overflow: hidden;
}

.lk-prejoin .lk-video-container video, .lk-prejoin .lk-video-container .lk-camera-off-note {
  display: block;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.lk-prejoin .lk-video-container video[data-lk-facing-mode=user] {
  transform: rotateY(-180deg);
}

.lk-prejoin .lk-video-container .lk-camera-off-note {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 100%;
  aspect-ratio: 16/10;
  background-color: #000;
  display: grid;
  place-items: center;
}

.lk-prejoin .lk-video-container .lk-camera-off-note > * {
  height: 70%;
  max-width: 100%;
}

.lk-prejoin .lk-audio-container {
  display: none;
}

.lk-prejoin .lk-audio-container audio {
  width: 100%;
  height: auto;
}

.lk-prejoin .lk-button-group-container {
  display: flex;
  flex-wrap: nowrap;
  gap: 1rem;
}

.lk-prejoin .lk-button-group-container > .lk-button-group {
  width: 50%;
}

.lk-prejoin .lk-button-group-container > .lk-button-group > .lk-button {
  justify-content: right;
}

.lk-prejoin .lk-button-group-container > .lk-button-group > .lk-button:first-child {
  width: 100%;
}

@media (max-width: 400px) {
  .lk-prejoin .lk-button-group-container {
    flex-wrap: wrap;
  }
  .lk-prejoin .lk-button-group-container > .lk-button-group {
    width: 100%;
  }
}
.lk-prejoin .lk-username-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  max-width: 100%;
}

.lk-prejoin .lk-join-button {
  --lk-control-fg: var(--lk-accent-fg);
  --lk-control-bg: var(--lk-accent-bg);
  --lk-control-hover-bg: var(--lk-accent2);
  --lk-control-active-bg: var(--lk-accent3);
  --lk-control-active-hover-bg: var(--lk-accent4);
  background-color: var(--lk-control-bg);
}

.lk-prejoin .lk-join-button:hover {
  background-color: var(--lk-control-hover-bg);
}

.lk-focus-layout-wrapper, .lk-grid-layout-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;
  height: calc(100% - var(--lk-control-bar-height));
}

.lk-grid-layout-wrapper {
  flex-direction: column;
  align-items: center;
}

.lk-focus-layout-wrapper {
  align-items: stretch;
}

.lk-video-conference {
  position: relative;
  display: flex;
  align-items: stretch;
  height: 100%;
}

.lk-video-conference-inner {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 100%;
}

.lk-settings-menu-modal {
  position: fixed;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  background: var(--lk-bg);
  padding: 1rem;
  border-radius: var(--lk-border-radius);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  background-color: var(--lk-bg);
  border: 1px solid var(--lk-border-color);
  border-radius: var(--lk-border-radius);
  box-shadow: var(--lk-box-shadow);
  min-width: 50vw;
  min-height: 50vh;
}

